import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { ModalModule, BsModalService, BsModalRef } from '../../../node_modules/ngx-bootstrap';
import { CookieService } from 'ngx-cookie-service';
import {TranslateService} from '../services/translate.service';
import {globals} from '../../environments/environment';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']

})
export class HeaderComponent implements OnInit {

  @ViewChild('generatemodal') generatemodal: ElementRef;
  modalRef: BsModalRef;
  config = {
    animated: true,
    keyboard: true,
    backdrop: true,
    ignoreBackdropClick: false,
    class: "my-modal"
  };
  cookieAccepted: boolean = false;
  constructor(
    private modalService: BsModalService, private spinnerService: Ng4LoadingSpinnerService,
    private cookieService: CookieService,private translate: TranslateService) {

  }

  ngOnInit() {
    //this.spinnerService.show();
    this.cookieAccepted = this.cookieService.check('cookieAccepted');
    const value: string = this.cookieService.get('cookieAccepted');
    if (value !== 'true') {
      this.cookieAccepted = false;
    }
    else { this.cookieAccepted = true; }

    this.setLang('en');
  }

  setLang(languageCode:any){ 
    this.translate.use(languageCode).then((tr) => {
        
        //globals.TitleSeeCompleteList = tr.TitleSeeCompleteList;
      });
  }

  openDownloadAppModal() {
    this.modalRef = this.modalService.show(this.generatemodal, this.config);
  }
  acceptCookie() {
    this.cookieService.set('cookieAccepted', 'true');
    this.cookieAccepted = !this.cookieAccepted;
  }
  openAndroid() {
    
    if (navigator.userAgent.match(/android/i)) {
      var androidLink= 'http://play.google.com/store/apps/details?id=com.carriertransicold.containerLINK';
      // var androidLink = 'market://details?id=com.carriertransicold.containerLINK';
      
      this.modalRef.hide();
      var win = window.open(androidLink, '_blank'); win.focus();
     
    }
    else if(navigator.userAgent.match(/iphone/i)){
      window.location.href = 'https://itunes.apple.com/us/app/containerlink/id1387492814';
      var win = window.open(androidLink, '_blank'); win.focus();
  }
  else {
    var Link = 'https://play.google.com/store/apps/details?id=com.carriertransicold.containerLINK';
    this.modalRef.hide();
    var win = window.open(Link, '_blank');    
    win.focus();
  }
  }
}
