import { Component, OnInit, ChangeDetectorRef, ViewChild, ElementRef } from '@angular/core';
import { dealerModel } from '../models/dealerModel';
import { DealerService } from '../services/dealer.service';
import { GeocodeService } from '../services/geocode.service';
import { DataServiceService } from '../services/data-service.service';
import { Subscription } from 'rxjs';
import { filterModel } from '../models/filterModel';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { dealerTimingModel } from '../models/dealerTimingModel';
import { locationModel } from '../models/locationModel';
import { advanceFilterModel } from '../models/advanceFilterModel';
import { BsModalService, BsModalRef } from '../../../node_modules/ngx-bootstrap';
import { EmailService } from '../services/email.service';
import { emailModel } from '../models/emailModel';
import { ErrorHandlerService } from '../services/error-handler.service';
import { ActivatedRoute } from '@angular/router';
//import { environment } from '../../environments/environment';
import { environment } from '../../environments/environment';
//import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-dealerlist',
  templateUrl: './dealerlist.component.html',
  styleUrls: ['./dealerlist.component.css'],
  providers: [DealerService, GeocodeService, EmailService]
})


export class DealerlistComponent implements OnInit {
  _selectedDealer: any = {};
  dealerArray: any[] = [];
  dealerCompleteArray: dealerModel[] = [];
  serviceTypeMobile: boolean = false;
  serviceTypeAPU: boolean = false;
  serviceTypeTruckTrailer: boolean = false;
  serviceTypeDealerLevelPath: any;
  //location: {lat: number, lng: number};

  //data:any;
  loc: locationModel;
  origin: locationModel;
  destination: locationModel;

  loading: boolean;
  loadData: boolean

  userLoclat: any;
  userLoclng: any;

  selectedRow: number = 0;
  message: filterModel;

  filterargs: filterModel;
  advSearchFilterArgs: advanceFilterModel;
  subscription: Subscription;
  subscriptionAdvSearchFilter: Subscription;

  checkStoreStatusObj: any;
  TodayTimingdata: any = {};
  TimingsObj: any[];
  finalTimingsObj: any[];

  filterObj: any = {};
  country: string[] = [];
  emailAlert: boolean = false;
  emailInputText: any;

  currentUrl: any;
  currentUrlDealerID: any;
  dealer: dealerModel;
  loadFullSite: boolean = true;
  @ViewChild('shareStoreDetailmodal') shareStoreDetailmodal: ElementRef;
  modalRef: BsModalRef;
  config = {
    animated: true,
    keyboard: true,
    backdrop: true,
    ignoreBackdropClick: false,
    class: "my-modal"
  };
  constructor(private _dealerService: DealerService,
    private geocodeService: GeocodeService,
    private ref: ChangeDetectorRef,
    private _dataService: DataServiceService,
    private spinnerService: Ng4LoadingSpinnerService,
    private modalService: BsModalService,
    private emailService: EmailService,
    private errorHandlerService: ErrorHandlerService,
    private route: ActivatedRoute//,private router : Router,
    //,locationPath: Location
  ) {
    //this.currentUrl = locationPath.path();

  }

  ngOnInit() {
    this.getCurrentUrlDealerID();
    //this.InitApp();
  }
  InitApp() {
    this.getFiltersFromContent();
    this._selectedDealer = new dealerModel();
    this._selectedDealer.dealerTimingModel = new dealerTimingModel();
    this.dealerArray = [];
  }

  getCurrentUrlDealerID() {

    this.currentUrlDealerID = this.route.snapshot.paramMap.get('id');
    if (this.currentUrlDealerID) {
      var uri = "https://dealerlocatorapp.azurewebsites.net/dealer/" + this.currentUrlDealerID;

      var encoded = encodeURI(uri);
      console.log('enc ' + encoded);

      try {
        console.log('dec ' + decodeURI(encoded));

      } catch (e) {
        console.error(e);
      }
      this.getDealerById();
    }
    else { this.InitApp(); }
  }
  getDealerById() {
    this._dealerService.getDealer(this.currentUrlDealerID).subscribe((data: dealerModel) => {
      this.dealer = data;
      console.log(this.dealer);
      this.dealerArray.push(this.dealer);
      this.selectDealer(this.dealerArray[0], 0);
      this._selectedDealer = this.dealerArray[0];
    });
  }

  setSelectedDealer() {
    this._selectedDealer = this.dealerArray[0];
    this.checkStoreStatusObj = this.checkStoreStatus();

  }
  selectDealer(dealer, index) {
    this._selectedDealer = dealer;
    this.selectedRow = index;
    this.setImagesServiceType();
    this.setDealerLevelImage();
    this.checkStoreStatusObj = this.checkStoreStatus();
  }

  setDealerLevelImage() {
    this.serviceTypeDealerLevelPath = "";
    if (this._selectedDealer) {
      if (this._selectedDealer.dealerLevel === "Gold") {
        this.serviceTypeDealerLevelPath = "assets/img/Gold award 2017.png";
      }
      else if (this._selectedDealer.dealerLevel === "Silver") {
        this.serviceTypeDealerLevelPath = "assets/img/Silver Award 2017.png";
      }
      else if (this._selectedDealer.dealerLevel === "Platinum") {
        this.serviceTypeDealerLevelPath = "assets/img/Platinum Award 2017.png";
      }
    }
  }

  getDirection() {
    //var red="https://www.google.es/maps/dir/'37.7699298,-122.4469157'/'37.7683909618184,-122.51089453697205'";
    let url;

    if (this.filterargs) {
      if (this.filterargs.userLat && this.filterargs.userLong && this._selectedDealer.dealerLatitude && this._selectedDealer.dealerLongitude) {
        url = "https://www.google.es/maps/dir/'" + this.filterargs.userLat + "," + this.filterargs.userLong + "'/'"
          + this._selectedDealer.dealerLatitude + "," + this._selectedDealer.dealerLongitude + "'";
        this.showLocationInMap(url);

      }
      else if (this._selectedDealer.dealerLatitude && this._selectedDealer.dealerLongitude) {
        url = "https://www.google.es/maps/dir/''/'"
          + this._selectedDealer.dealerLatitude + "," + this._selectedDealer.dealerLongitude + "'";
        this.showLocationInMap(url);
      }

    }

    else if (this._selectedDealer.dealerLatitude && this._selectedDealer.dealerLongitude) {
      url = "https://www.google.es/maps/dir/''/'"
        + this._selectedDealer.dealerLatitude + "," + this._selectedDealer.dealerLongitude + "'";
      this.showLocationInMap(url);
    }
  }

  showLocationInMap(url: any) {
    var win = window.open(url, '_blank');
    win.focus();
  }

  getFiltersFromContent() {
    this.subscription = this._dataService.getMessage().subscribe(message => {

      this.filterargs = message.text;


      if (this.dealerCompleteArray.length !== 0) {
        this.dealerCompleteArray.forEach(element => {
          if(element.dealerLatitude){
            element.distance="";
          }
          
        });
        this.filterDealerArray(this.dealerCompleteArray, this.filterargs);
      }
      else {
        this._dealerService.getAllDealers().subscribe((data: dealerModel[]) => {
          this.dealerCompleteArray = data;
          console.log(this.dealerCompleteArray);
          if (this.dealerCompleteArray) {
            this.filterDealerArray(this.dealerCompleteArray, this.filterargs);
          }
        });
      }
    });

    this.subscriptionAdvSearchFilter = this._dataService.getAdvanceSearchFilterMessage().subscribe(message => {
      this.advSearchFilterArgs = message.text;

      if (this.dealerCompleteArray.length !== 0) {
        this.filterDealersAdvanceSearch(this.dealerCompleteArray, this.advSearchFilterArgs);
      }
      else {
        this._dealerService.getAllDealers().subscribe((data: dealerModel[]) => {
          this.dealerCompleteArray = data;
          if (this.dealerCompleteArray) {
            this.filterDealersAdvanceSearch(this.dealerCompleteArray, this.advSearchFilterArgs);
          }
        });
      }
    });
  }

  filteredByCountry: dealerModel[];
  currentLocation: locationModel;
  filterDealerArray(items: any, filter: filterModel) {
    if (filter && items.length > 0) {
      this.country = this._dataService.getSelectedCountries(filter.region);
      this.filteredByCountry = this._dataService.filterCountryData(items, this.country);

      if (filter.inputText) {
        var location = filter.inputText.trim();
        this.currentLocation = new locationModel();
        this.filterObj = filter;
        this.addressToCoordinates(location);
      }
      else {
        this.currentLocation = new locationModel();
        var result = [];
        if (filter.userLat && filter.userLong) {
          this.currentLocation.lat = filter.userLat;
          this.currentLocation.lng = filter.userLong;
          result = this.applyHaversine(this.filteredByCountry).sort(this.compare);
          if (filter.distance) {
            result = this.filterByDistance(result, filter.distance);
            if (result)
              this.selectDealer(result[0], 0);
          }
        }
        else {
          result = this.filteredByCountry;
          result = this.filterBracketsPhoneMobileFax(result);
        }

        if (filter.dealerServices && filter.dealerServices.length > 0) {
          result = this.filterByServices(result, filter.dealerServices);
          if (result)
            this.selectDealer(result[0], 0);
        }

        if (result) {
          this.selectDealer(result[0], 0);
          this.dealerArray = result;
        }
      }
    }
    else {
      this.dealerArray = [];
    }
  }

  filterBracketsPhoneMobileFax(list: any) {
    var result = [];
    list.forEach(element => {
      element.phone = element.phone.replace('()', '');
      element.mobile = element.mobile.replace('()', '');
      element.fax = element.fax.replace('()', '');
      result.push(element);
    });
    return result;
  }

  filterDealersAdvanceSearch(items: dealerModel[], filter: advanceFilterModel) {
    if (items.length > 0 && filter) {

      var result = [];
      if (filter.postalCode && filter.postalCode.length > 0) {
        this.dealerArray = items.filter(a => {
          if (a.dealerAddressDetailModel)
            return a.dealerAddressDetailModel.postalCode === filter.postalCode;
        });

        if (this.dealerArray)
          this.selectDealer(this.dealerArray[0], 0);

      }
      else {

        this.country = this._dataService.getSelectedCountries(filter.region);
        items = this._dataService.filterCountryData(items, this.country);



        if (filter.selectedStates && filter.selectedStates.length > 0) {
          items.filter(a => {
            if (a.dealerAddressDetailModel) {
              filter.selectedStates.forEach(element => {
                if (element.name.toLowerCase() == a.dealerAddressDetailModel.state.toLowerCase()) {
                  result.push(a);
                }
              });
            }
          });
        }
        else {
          result = items;
          result = this.filterBracketsPhoneMobileFax(result);
        }
        if (filter.selectedCities && filter.selectedCities.length > 0) {
          var cityResult = [];
          result.filter(a => {
            if (a.dealerAddressDetailModel) {
              filter.selectedCities.forEach(element => {
                if (element.toLowerCase() == a.dealerAddressDetailModel.city.toLowerCase()) {
                  cityResult.push(a);
                }
              });
            }
          });
          result = cityResult;
        }

        if (filter.dealerServices && filter.dealerServices.length > 0) {
          result = this.filterByServices(result, filter.dealerServices);
        }
        if (filter.distance) {
          result = this.filterByDistance(result, filter.distance);
        }

        if (result.length > 0) {
          result = result.sort(this.compare);

          this.dealerArray = Array.from(new Set(result));

          if (this.dealerArray)
            this.selectDealer(this.dealerArray[0], 0);
        }
        else
          this.dealerArray = [];
      }
    }
  }

  setImagesServiceType() {
    if (this._selectedDealer) {
      this.serviceTypeMobile = false;
      this.serviceTypeTruckTrailer = false;
      this.serviceTypeAPU = false;

      let dealerServiceTypes = this._selectedDealer.dealerServiceTypeModel;
      if (dealerServiceTypes) {
        for (let ServiceType of dealerServiceTypes) {
          if (ServiceType.serviceType_uid == "1") {
            this.serviceTypeMobile = true;
          }
          else if (ServiceType.serviceType_uid == "2") {
            this.serviceTypeTruckTrailer = true;
          }
          else if (ServiceType.serviceType_uid == "3") {
            this.serviceTypeAPU = true;
          }
        }
      }
    }
  }

  goToSpecificUrl(): void {
    var win = window.open(this._selectedDealer.webUrl, '_blank');
    win.focus();
  }

  checkStoreStatus(): boolean {
    if (this._selectedDealer)
      if (this._selectedDealer.dealerTimingModel) {
        var d = new Date();

        var day;
        switch (+d.getDay()) {
          case 1:
            {
              day = "Monday"
              break;
            } case 2:
            {
              day = "Tuesday"
              break;
            }
          case 3:
            {
              day = "Wednesday"
              break;
            }
          case 4:
            {
              day = "Thursday"
              break;
            }
          case 5:
            {
              day = "Friday"
              break;
            }
          case 6:
            {
              day = "Saturday"
              break;
            }
          case 7:
            {
              day = "Sunday"
              break;
            }
        }
        this.TimingsObj = new Array<any>();

        this._selectedDealer.dealerTimingModel.forEach(element => {
          var obj = new dealerTimingModel();
          obj.startTime = element.startTime;
          obj.endTime = element.endTime;
          obj.openingDay = element.openingDay;
          this.TimingsObj.push(obj);
        });
        let resp = new Array<any>();
        for (var i = 0; i < this.TimingsObj.length; i++) {

          //resp=this.TimingsObj[i];
          if (resp.length > 0) {
            var length = resp.length;
            if (this.TimingsObj[i].startTime == resp[length - 1].startTime && this.TimingsObj[i].endTime == resp[length - 1].endTime) {

              resp[length - 1].openingDay = resp[length - 1].openingDay + '-' + this.TimingsObj[i].openingDay;
            }
            else {
              resp.push(this.TimingsObj[i]);
            }
          }
          else {
            resp.push(this.TimingsObj[i]);
          }
        }

        for (var i = 0; i < resp.length; i++) {
          var occurance = resp[i].openingDay.split("-").length - 1
          if (resp[i].startTime === "") {
            resp[i].startTime = "Close";
          }
          if (occurance > 1) {
            var left = resp[i].openingDay.substring(0, resp[i].openingDay.indexOf("-"));
            var right = resp[i].openingDay.substring(resp[i].openingDay.length, resp[i].openingDay.lastIndexOf("-"));
            resp[i].openingDay = left + right;
          }
        }

        resp.forEach(day => {
          if (day.startTime && day.endTime)
            if (day.startTime !== "Close") {
              day.startTime = day.startTime + ' Hrs. to ' + day.endTime + ' Hrs.'
            }

        });

        this.finalTimingsObj = resp;


        this.TodayTimingdata = this._selectedDealer.dealerTimingModel.filter(a => a.openingDay === day);


        var open = this.addZero(this.TodayTimingdata[0].startTime);
        var close = this.addZero(this.TodayTimingdata[0].endTime);//.format('H:mm');

        // this.TodayTimingdata[0].forEach(day => {
        //   if(this.TodayTimingdata[0].startTime && this.TodayTimingdata[0].endTime)
        //   this.TodayTimingdata[0].startTime= ' '+this.TodayTimingdata[0].startTime +' Hrs. to '+ this.TodayTimingdata[0].endTime +' Hrs.'
        // });

        this.TodayTimingdata[0].startTime = '    ' + this.TodayTimingdata[0].openingDay + ' ' + this.TodayTimingdata[0].startTime + ' Hrs. to ' + this.TodayTimingdata[0].endTime + ' Hrs.';

        var currentDate = new Date();
        var currentTime = currentDate.getHours() + ':' + currentDate.getMinutes();

        if (currentTime >= open && currentTime <= close) {
          return true;
        }
        return false;
      }
  }
  addZero(i) {
    if (i < 10) {
      i = "0" + i;
    }
    return i;
  }


  filterByDistance(items: any, distance: any) {
    switch (+distance) {
      case 1: {
        return items;
        break;
      }
      case 2: {
        return items.filter(a => a.distance <= 50);
        break;
      }
      case 3: {
        return items.filter(a => a.distance <= 100);
        break;
      }
      case 4: {
        return items.filter(a => a.distance <= 150);
        break;
      }
      case 5: {
        return items.filter(a => a.distance <= 200);
        break;
      }
      case 6: {
        return items.filter(a => a.distance <= 250);
        break;
      }
      default: {
        return items;
      }
    }
  }

  filterByServices(dealers: dealerModel[], services: any) {
    if (services) {
      var result = [];
      dealers.forEach((item) => {
        if (item.dealerServiceTypeModel) {
          item.dealerServiceTypeModel.forEach(dealerService => {
            for (var service of services) {
              if (service.id === dealerService.serviceType_uid) {
                result.push(item);
              }
            }
          });
        }
      })
      return result = Array.from(new Set(result));
    }
    else { return dealers; }
  }

  addressToCoordinates(address) {
    this.geocodeService.geocodeAddress(address)
      .toPromise().then(
        location => {
          this.loc = location;
          this.assignLatLng();
          this.ref.detectChanges();
        }
      );
  }

  assignLatLng() {
    if (this.loc.lat && this.loc.lng) {

      this.filterObj.userLat = this.currentLocation.lat = this.loc.lat;
      this.filterObj.userLong = this.currentLocation.lng = this.loc.lng;

      var result = [];
      result = this.applyHaversine(this.filteredByCountry).sort(this.compare);

      if (this.filterObj.distance) {
        result = this.filterByDistance(result, this.filterObj.distance);

      }
      if (this.filterObj.dealerServices && this.filterObj.dealerServices.length > 0) {
        result = this.filterByServices(result, this.filterObj.dealerServices);

      }
      if (result) {
        this.selectDealer(result[0], 0);
        this.dealerArray = result;
      }

    }
    else {
      alert("Location Not Found");
      this.dealerArray = [];
    }
  }

  applyHaversine(locations) {
    if (this.currentLocation.lat && this.currentLocation.lng) {
      locations.map((location) => {
        location.phone = location.phone.replace("()", '');
        location.mobile = location.mobile.replace("()", '');
        location.fax = location.fax.replace("()", '');
        let placeLocation = {
          lat: location.dealerLatitude,
          lng: location.dealerLongitude
        };
        if (placeLocation.lat !== 0 && placeLocation.lng !== 0) {
          location.distance = this.getDistanceBetweenPoints(
            this.currentLocation.lat, this.currentLocation.lng,
            placeLocation,
            'miles'
          ).toFixed(1);
        }
      });
      this.spinnerService.hide();
      return locations;
    }

  }

  getDistanceBetweenPoints(lat1, lon1, end, unit) {
    var radlat1 = Math.PI * lat1 / 180
    var radlat2 = Math.PI * end.lat / 180
    var theta = lon1 - end.lng
    var radtheta = Math.PI * theta / 180
    var dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
    dist = Math.acos(dist)
    dist = dist * 180 / Math.PI
    dist = dist * 60 * 1.1515
    if (unit == "K") { dist = dist * 1.609344 }
    if (unit == "miles") { dist = dist * 0.8684 }
    return dist
  }

  toRad(x) {
    return x * Math.PI / 180;
  }

  compare(a, b) {
    if (Number(a.distance) < Number(b.distance))
      return -1;
    if (Number(a.distance) > Number(b.distance))
      return 1;
    return 0;
  }

  openShareStoreDetailmodal() {
    this.modalRef = this.modalService.show(this.shareStoreDetailmodal
      // ,{ class: 'shareStoreModalClass' }
      , this.config
    );
    this.emailAlert = false;
  }

  isRequired() {
    return false;
  }

  sendEmail(email: any, emailText: any) {
    this.emailAlert = false;
    if (email && email.length > 0) {
      var emailObj = new emailModel();
      emailObj.emailAddress = email;
      emailObj.emailMessage = emailText;
      emailObj.storeName = this._selectedDealer.name;
      emailObj.storeWebUrl = this._selectedDealer.webUrl;
      emailObj.dealerLocatorLink = environment.webBaseUrl + 'dealer/' + this._selectedDealer.uid;
      if (this._selectedDealer.dealerAddressDetailModel) {

        var address1 = this._selectedDealer.dealerAddressDetailModel.address1;
        var address2 = this._selectedDealer.dealerAddressDetailModel.address2;
        var city = this._selectedDealer.dealerAddressDetailModel.city;
        var state = this._selectedDealer.dealerAddressDetailModel.state;
        var postalCode = this._selectedDealer.dealerAddressDetailModel.postalCode;

        emailObj.storeAddress =
          address1
          + ' ' + address2
          + ' ' + city
          + ' ' + state
          + ' ' + postalCode
          + ' ' + this._selectedDealer.country;


        this.emailService.sendEmail(emailObj)
          .subscribe((msg: any) => {
            alert(msg);
            this.modalRef.hide()
          })
          //.catch(err => console.error(err));
          , error => this.errorHandlerService.handleError(error);
      }
    }
    else {
      this.emailAlert = true;
    }
  }
}













